<template>
  <v-btn @click="toggleTheme">toggle theme</v-btn>
</template>
<script>
import {useTheme} from 'vuetify'
export default {
  name: "changeTheme",
  setup() {
    const theme = useTheme()

    return {
      theme,
      toggleTheme: () => theme.global.name.value = theme.global.current.value.dark ? 'light' : 'dark'
    }
  },
}
</script>

