<template>
  <div>
    <template v-for="exhibition in source" v-bind:key="exhibition.topic">
      <v-btn
          variant="outlined"
          @click="loadData(exhibition.topic)"
      >
        {{ exhibition.name }}
      </v-btn>
    </template>
  </div>

  <v-data-table
      v-if="jsonData"
      v-model:page="page"
      v-model:items-per-page="itemsPerPage"
      :headers="headers"
      :items="itemData"
      :item-key="item => item.UUID"
  >
    <template v-slot:item="{ item }">
      <tr>
        <td>{{ item.columns.systematics }}</td>
        <td><a :href="item.columns.source_url" target="_blank">{{ item.columns.title }}</a></td>
        <td>{{ item.columns.date }}</td>
        <td>{{ item.columns.address }}</td>
        <td>
          <v-img :src="item.columns.figure" :width="300"></v-img>
        </td>
      </tr>
    </template>
  </v-data-table>

  <div v-else>
    <p>请点击按钮加载数据</p>
  </div>
</template>

<script>
import {defineComponent} from 'vue';
import {VDataTable} from 'vuetify/labs/VDataTable'

import {exhibitionTopicClassObject, URL_SOURCE_OBJECT} from '../assets/source.js'


export default defineComponent({
  name: 'playTable',
  components: {
    'v-data-table': VDataTable,
  },
  data() {
    return {
      page: 1,
      loading: true,
      itemsPerPage: 5,
      headers: [
        {title: '分類', key: 'systematics'},
        {title: '名稱', key: 'title'},
        {title: '日期', key: 'date'},
        {title: '地點', key: 'address'},
        {title: '縮圖', key: 'figure'},
      ],
      itemData: null,
      jsonData: null,
      source: exhibitionTopicClassObject,
    };
  },
  methods: {
    loadData(url) {
      fetch(`${URL_SOURCE_OBJECT}${url}.json`)
          .then(response => response.json())
          .then(data => {
            this.jsonData = data;
            this.itemData = this.jsonData.data;
          })
          .catch(error => {
            console.error(error);
          });
    }
  },

})
</script>

